
.work-table {
    display: -webkit-flex;
    display: flex;
    min-height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.house-card__img {
  cursor: pointer;
  object-fit: cover;
  height: 100%;
  width: 100%; }
  .house-card__img-wrap {
    position: relative; }
    .house-card__img-wrap::before {
      display: block;
      content: "";
      width: 100%;
      padding-top: 100%; }
    .house-card__img-wrap-ratio {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0; }
  .house-card__img-indicator {
    display: inline-block;
    width: 7px;
    height: 7px;
    border: 2px solid #fff;
    border-radius: 50%;
    margin: 4px;
    box-shadow: 0 0 5px black; }
    .house-card__img-indicator.current {
      background: #fff; }
    .house-card__img-indicator-list {
      position: absolute;
      display: -webkit-flex;
      display: flex;
      list-style: none;
      padding: 0;
      -webkit-justify-content: center;
              justify-content: center;
      bottom: 5px;
      width: 100%; }

.house-card__address {
  font-size: 0.9em; }

.house-card__price {
  font-size: 1.3em;
  font-weight: bold;
  margin-bottom: 0.7em; }
  .house-card__price:last-child {
    margin-bottom: 0; }
  .house-card__img-wrap .house-card__price {
    position: absolute;
    top: 20px;
    left: 0;
    margin: 0 20px;
    color: #fff;
    text-shadow: 0 0 5px #000; }

.house-card__area {
  font-size: 0.8em;
  color: #4b4b4b;
  margin-top: 1em; }

